import React, { Component } from 'react';
import { connect } from 'react-redux';
import { setSearchField, requestRobots } from '../actions';

import CardList from '../components/CardList';
import SearchBox from '../components/SearchBox';
import Scroll from '../components/Scroll';
import ErrorBoundry from '../components/ErrorBoundry';

import './App.css';

// parameter state comes from index.js provider store state(rootReducers)
const mapStateToProps = (state) => {
  return {
    searchField: state.searchRobots.searchField,
    robots: state.requestRobots.robots,
    isPending: state.requestRobots.isPending
  }
}

// dispatch the DOM changes to call an action. note mapStateToProps returns object, mapDispatchToProps returns function
// the function returns an object then uses connect to change the data from redecers.
const mapDispatchToProps = (dispatch) => {
  return {
    onSearchChange: (event) => dispatch(setSearchField(event.target.value)),
    onRequestRobots: () => dispatch(requestRobots())
  }
}

let imageCapture = null;
let mediaStream = null;

navigator.mediaDevices.getUserMedia({video: true})
  .then(function(stream) {
    /* use the stream */
    mediaStream = stream;
    console.log('mediaStream', mediaStream);
    const video = document.querySelector('video');
    video.srcObject = mediaStream;
    video.onloadedmetadata = function(e) {
      console.log('video play');
      video.play();
    };

    console.log('mediaStream getVideoTracks', mediaStream.getVideoTracks());
    
    let mediaStreamTrack = null;
    if (mediaStream.getVideoTracks().length > 1) {
      mediaStreamTrack = mediaStream.getVideoTracks()[1];
    } else {
      mediaStreamTrack = mediaStream.getVideoTracks()[0];
    }
    
    imageCapture = new ImageCapture(mediaStreamTrack);
    console.log(imageCapture);
  })
  .catch(function(err) {
    /* handle the error */
    console.log('error', err);
  });

const takePhoto = () => {
  const img = document.querySelector('img');

  imageCapture.takePhoto()
      .then(blob => {
        img.src = URL.createObjectURL(blob);
        img.onload = () => { URL.revokeObjectURL(img.src); }
      })
      .catch(error => console.error('takePhoto() error:', error));
}

class App extends Component {
  componentDidMount() {
    this.props.onRequestRobots();
  }

  render() {
    const { robots, searchField, onSearchChange, isPending } = this.props;
    const filteredRobots = robots.filter(robot => {
      return robot.name.toLowerCase().includes(searchField.toLowerCase());
    })
    const supportedConstraints = navigator.mediaDevices.getSupportedConstraints();
    console.log('mediaStream in render', mediaStream);
    return (
      <div className='tc'>
        <h1 className='f1'>RoboFriends</h1>
        <video width="320" height="240"></video>
        <button onClick={takePhoto}>Take Photo</button>
        <img width="320" height="240" />
        <div id="constraintList">
        {
          Object.keys(supportedConstraints).map((constraint) => {
            return (<code>{constraint}<br></br></code>);
          })
        }
        </div>
        {/* <SearchBox searchChange={onSearchChange}/>
        <Scroll>
          { isPending ? <h1>Loading</h1> :
            <ErrorBoundry>
              <CardList robots={filteredRobots} />
            </ErrorBoundry>
          }
        </Scroll> */}
      </div>
    );
  }
}

// action done from mapDispatchToProps will channge state from mapStateToProps
export default connect(mapStateToProps, mapDispatchToProps)(App)
